import { Reducer } from 'redux';

/**
 * REDUX ACTIONS
 */

enum BaseLinkActionType {
  BASELINK_UPDATED = 'BASELINK_UPDATED'
}

export interface IBaseLinkUpdatedAction {
  type: BaseLinkActionType;
  baseLink: string;
}

export type BaseLinkAction = IBaseLinkUpdatedAction;

export const BaseLinkUpdatedAction = (baseLink: string): BaseLinkAction => {
  return {
    type: BaseLinkActionType.BASELINK_UPDATED,
    baseLink: baseLink
  };
};

/**
 * REDUX REDUCER
 */
export const BaseLinkReducer: Reducer<string> = (baseLink: string | null = null, action: BaseLinkAction) => {
  switch (action.type) {
    case BaseLinkActionType.BASELINK_UPDATED:
      return action.baseLink;
    default:
      return baseLink;
  }
};
