import { Reducer } from 'redux';

/**
 * REDUX ACTIONS
 */

enum NextLinkActionType {
  NEXTLINK_UPDATED = 'NEXTLINK_UPDATED'
}

export interface INextLinkUpdatedAction {
  type: NextLinkActionType;
  nextLink: string;
}

export type NextLinkAction = INextLinkUpdatedAction;

export const NextLinkUpdatedAction = (nextLink: string): NextLinkAction => {
  return {
    type: NextLinkActionType.NEXTLINK_UPDATED,
    nextLink
  };
};

/**
 * REDUX REDUCER
 */
export const NextLinkReducer: Reducer<string> = (nextLink: string | null = null, action: NextLinkAction) => {
  switch (action.type) {
    case NextLinkActionType.NEXTLINK_UPDATED:
      return action.nextLink;
    default:
      return nextLink;
  }
};
