import { Reducer } from 'redux';
import { ITreePosition, IDriveItemTree } from '../definitions';

//#region action

enum PositionActionType {
  POSITION_UPDATED = 'POSITION_UPDATED'
}

export interface IPositionUpdatedAction {
  type: PositionActionType;
  position: ITreePosition[];
}

export type PositionAction = IPositionUpdatedAction;

export const PositionUpdatedAction = (position: ITreePosition[]): PositionAction => {
  return {
    type: PositionActionType.POSITION_UPDATED,
    position
  };
};

//#endregion

//#region reducer

export const PositionReducer: Reducer<ITreePosition[]> = (
  position: ITreePosition[] = [],
  action: PositionAction
) => {
  switch (action.type) {
    case PositionActionType.POSITION_UPDATED:
      return [...action.position];
    default:
      return [...position];
  }
};

//#endregion
