import * as React from 'react';
import { Breadcrumb, IBreadcrumbItem } from 'office-ui-fabric-react/lib/Breadcrumb';
import { IDocumentsAppState } from '../redux/store';
import { connect } from 'react-redux';
import { ITreePosition, IEnv, IDriveItemTree, IDriveItemTreeItem } from '../definitions';
import { PositionUpdatedAction } from '../redux/position';
import { ItemsThunk } from '../redux/tree';
import { getTreeItem } from '../common/helpers';

export interface IFolderNavigationProps {
  items: IBreadcrumbItem[];
}

interface IPropsFromState {
  position: ITreePosition[];
  tree: IDriveItemTree;
}

interface IPropsFromDispatch {
  getFolder: (position: ITreePosition[]) => void;
}

interface IOwnProps {
  env: IEnv;
}

export const _FolderNavigation: React.SFC<IFolderNavigationProps> = props => {
  const { items } = props;
  return (
    <React.Fragment>
      <Breadcrumb
        items={items}
        ariaLabel="Folder navigation (Breadcrumb) for Documents app"
        overflowAriaLabel="More links"
        maxDisplayedItems={2}
        overflowIndex={items.length > 2 ? 1 : 0}
        styles={{
          root: ['pintraBreadCrumbRoot', {}],
          list: ['pintraBreadCrumbList', {}],
          listItem: ['pintraBreadCrumbListItem', {}],
          chevron: ['pintraBreadCrumbChevron', {}],
          overflow: ['pintraBreadCrumbOverflow', {}],
          overflowButton: ['pintraBreadCrumbOverflowButton', {}],
          item: ['pintraBreadCrumbItem', {}],
          itemLink: [
            'pintraBreadCrumbItemLink',
            {
              color: 'inherit',
              background: 'inherit',
              backgroundColor: 'inherit',
              selectors: {
                ':focus': {
                  background: 'inherit',
                  backgroundColor: 'inherit'
                }
              }
            }
          ]
        }}
      />
    </React.Fragment>
  );
};

const mapStateToProps = (state: IDocumentsAppState) => ({
  position: state.position,
  tree: state.tree
});

const mapDispatchToProps = (dispatch: any, ownProps: IOwnProps) => {
  const { env } = ownProps;
  return {
    /**
     * Updates the tree position after the user clicks in the breadcrumb and
     * then updates the next link to navigate back up the tree
     */
    getFolder: (position: ITreePosition[]) => {
      dispatch(PositionUpdatedAction(position));
    }
  };
};

const mergeProps = (stateProps: IPropsFromState, dispatchProps: IPropsFromDispatch, ownProps: IOwnProps) => {
  /**
   * The items of the breadcrumb come from the current position that
   * is stored in the redux store. The very first item, however, is
   * a special item and gets the name of the library instead and its
   * click target is an empty position to indicate the root of that
   * library.
   */
  const { listTitle } = ownProps.env.props;
  const items: IBreadcrumbItem[] = [
    {
      text: listTitle,
      key: '0',
      onClick: (event: React.MouseEvent<HTMLElement>) => {
        dispatchProps.getFolder([]);
        event.stopPropagation();
      }
    }
  ];
  const { position, tree } = stateProps;
  const currPos: ITreePosition[] = [];

  for (let i = 0; i < position.length; i++) {
    currPos.push(position[i]);
    const treeItem = getTreeItem(tree, currPos) as IDriveItemTreeItem;
    if (!treeItem) {
      console.debug(`Unexpected: No tree item was returned for position ${currPos}.`);
    }
    items.push({
      text: treeItem.item.name,
      key: (i + 1).toString(),
      onClick: (event: React.MouseEvent<HTMLElement>) => {
        if (i == position.length - 1) return;
        currPos.pop();
        dispatchProps.getFolder(currPos);
        event.stopPropagation();
      }
    });
  }

  return {
    items
  };
};

export const FolderNavigation = connect<IPropsFromState, IPropsFromDispatch, IOwnProps, IFolderNavigationProps>(
  mapStateToProps,
  mapDispatchToProps,
  mergeProps
)(_FolderNavigation);
