import { IDriveItemTree, ITreePosition, IDriveItemTreeItem, IRootDriveItem } from '../definitions';

/**
 * Gets the item from the tree by navigating the tree from the root
 * using the position parameter to get to the correct branch (= folder)
 * and return (the folder's children).
 *
 * @param tree
 * @param position
 *
 * @returns IDriveItemTreeItem[] An array of items for a folder
 */
export const getCurrentBranch = (tree: IDriveItemTree, position: ITreePosition[]): IDriveItemTreeItem[] => {
  let currentBranch: IDriveItemTreeItem[] = tree.root.children;

  for (let i = 0; i < position.length; i++) {
    currentBranch = currentBranch[position[i].key].children;
  }

  return currentBranch;
};

/**
 * Gets the item from the tree by navigating the tree from the root
 * using the position parameter to get to the correct branch (= folder)
 * and then using the key of the last position element to get the item.
 *
 * @param tree
 * @param position
 *
 * @returns IDriveItemTreeItem | null
 */
export const getTreeItem = (
  tree: IDriveItemTree,
  position: ITreePosition[]
): IDriveItemTreeItem | IRootDriveItem => {
  let treeItem: IDriveItemTreeItem | IRootDriveItem = tree.root;

  for (let i = 0; i < position.length; i++) {
    treeItem = treeItem.children[position[i].key];
  }

  return treeItem;
};

/**
 *
 * @param tree
 * @param position
 * @param item
 */
export const updateTreeItem = (
  tree: IDriveItemTree,
  position: ITreePosition[],
  item: IDriveItemTreeItem
): void => {
  let treeItem: IDriveItemTreeItem | IRootDriveItem = tree.root;

  for (let i = 0; i < position.length; i++) {
    if (i == position.length - 1) {
      treeItem.children[position[i].key] = item;
      return;
    }
    treeItem = treeItem.children[position[i].key];
  }
};
