import { IOdataTypeDriveItem } from '../../common/odataTypes';

export const getIcon = (item: IOdataTypeDriveItem): string => {
  if (!!item.folder) {
    return 'FolderHorizontal';
  }
  if (!!item.file && item.name) {
    const fileExtension = item.name.substr(item.name.lastIndexOf('.') + 1).toLowerCase();

    if ('doc,docx,docm,docb,dot,dotm'.indexOf(fileExtension) > -1) {
      return 'WordDocument';
    }

    if ('xls,xlsx,xlst,xlt,xlsm,xltx,xltm,xslb,xla,xlam,xll,xlw,ods,xlr'.indexOf(fileExtension) > -1) {
      return 'ExcelDocument';
    }

    if ('ppt,pptx,pptm,potx,potm,ppam,ppsx,ppsm,sldx,sldm'.indexOf(fileExtension) > -1) {
      return 'PowerPointDocument';
    }

    if ('pub'.indexOf(fileExtension) > -1) {
      return 'PublisherLogo';
    }

    if (fileExtension.indexOf('pdf') > -1) {
      return 'PDF';
    }

    if ('one,notebooks'.indexOf(fileExtension) > -1) {
      return 'OneNoteLogo';
    }

    if ('png,jpg,jpeg,tif,psd,ps,ai,gif,bmp,svg,ico'.indexOf(fileExtension) > -1) {
      return 'Photo2';
    }

    if ('avi,flv,h264,m4v,mov,mp4,mpg,mpeg,swf,vob,wmv,3gp,3g2'.indexOf(fileExtension) > -1) {
      return 'MyMoviesTV';
    }

    if ('aif,cda,mid,mp3,mpa,ogg,wav,wma,wpl'.indexOf(fileExtension) > -1) {
      return 'MusicInCollection';
    }

    if ('odt,rtf,txt,tex,wpd,log,cfg,'.indexOf(fileExtension) > -1) {
      return 'Document';
    }

    if ('zip,7z,arj,deb,pkg,rar,rpm,tar,bin,iso,dmg,vcd,toast,bak,cab,jar'.indexOf(fileExtension) > -1) {
      return 'ZipFolder';
    }

    if ('csv,sql'.indexOf(fileExtension) > -1) {
      return 'Database';
    }

    if ('exe,com,lib,bat,ps1,cgi,pl,msi,dll'.indexOf(fileExtension) > -1) {
      return 'CommandPrompt';
    }

    if ('lnk'.indexOf(fileExtension) > -1) {
      return 'Link';
    }
  }

  if (!!item && !!item.package && !!item.package.type && item.package.type.toLowerCase() == 'onenote') {
    return 'OneNoteLogo';
  }

  return 'Unknown';
};
