import { ActionCreator, Action, Reducer } from 'redux';
import { ThunkAction, ThunkDispatch } from 'redux-thunk';
import { IDocumentsAppState } from './store';
import { FilesService } from '../services/FilesService';
import { StatusChangedAction } from '../../common/redux/Status';
import { StatusType } from '../../common/definitions';
import { IOdataTypeDriveItem } from '../../common/odataTypes';
import { IEnv, ITreePosition, IDriveItemTreeItem } from '../definitions';
import { IDriveItemTree } from '../definitions';
import { getCurrentBranch, getTreeItem } from '../common/helpers';
import { NextLinkUpdatedAction } from './nextLink';

//#region action

enum TreeActionType {
  TREE_UPDATED = 'TREE_UPDATED'
}

export interface ITreeUpdatedAction {
  type: TreeActionType;
  tree: IDriveItemTree;
}

export type TreeAction = ITreeUpdatedAction;

export const TreeUpdatedAction = (tree: IDriveItemTree): TreeAction => {
  return {
    type: TreeActionType.TREE_UPDATED,
    tree
  };
};

//#endregion

//#region reducer

export const TreeReducer: Reducer<IDriveItemTree> = (
  tree: IDriveItemTree = { root: { children: [], locked: false } },
  action: TreeAction
) => {
  switch (action.type) {
    case TreeActionType.TREE_UPDATED:
      return { ...action.tree };
    default:
      return { ...tree };
  }
};

//#endregion

export const ItemsThunk: ActionCreator<ThunkAction<void, IDocumentsAppState, void, Action>> = (
  env: IEnv,
  position: ITreePosition[]
) => {
  return (dispatch: ThunkDispatch<IDocumentsAppState, void, Action>, getState: () => IDocumentsAppState): void => {
    const { nextLink, tree } = getState();

    /**
     * Do not retrieve items twice
     */
    if (getTreeItem(tree, position).locked) {
      console.debug(`Not getting files for position ${position} because item is already locked.`);
      return;
    }

    /**
     * Callback to load the next batch
     *
     * @param nextLink
     */
    const dispatchNextLinkUpdatedAction = (nextLink: string) => {
      dispatch(NextLinkUpdatedAction(nextLink));
      dispatch(ItemsThunk(env, position));
    };

    /**
     * Callback to update the status after the last batch.
     */
    const dispatchStatusReadyAction = () => {
      dispatch(
        StatusChangedAction({
          type: StatusType.READY,
          statusMessage: ''
        })
      );
    };

    if (!nextLink) {
      dispatch(
        StatusChangedAction({
          type: StatusType.ERROR,
          statusMessage: 'Not able to connect to the requested SharePoint library.'
        })
      );
      console.debug('Unexpected: No next link @ItemsTunk.');
      return;
    }

    const filesService = new FilesService();
    filesService
      .getFiles(nextLink, env, dispatchNextLinkUpdatedAction, dispatchStatusReadyAction)
      .then((items: IOdataTypeDriveItem[] | null) => {
        if (items == null) {
          throw 'Not able to retrieve files from the requested SharePoint library.';
        }

        // Get current tree and current branch from store and prepare
        // for adding the new drive items to the current branch of that tree
        const { tree } = getState();
        const branch = getCurrentBranch(tree, position);
        const lastKey = branch.length;
        const _branch = [...branch];

        for (let i = 0; i < items.length; i++) {
          _branch.push({
            key: lastKey + i,
            item: items[i],
            children: [],
            locked: false
          });
        }

        const _tree = { ...tree };

        // Now place that branch back into the tree as root
        if (position.length == 0) {
          _tree.root.children = _branch;
        }
        // Or as one of the branches (a folder) that can be accessed
        // through the children property of the parent
        else {
          let treeIndex = '';

          for (let j = 0; j < position.length; j++) {
            treeIndex += `[${position[j].key}].children`;
          }

          eval(`_tree.root.children${treeIndex} = _branch`);
        }

        dispatch(TreeUpdatedAction(_tree));
      })
      .catch((err: any) => {
        dispatch(
          StatusChangedAction({
            type: StatusType.ERROR,
            statusMessage: 'Unexpected: Could not fetch your documents. Please contact your administrator.'
          })
        );
        console.debug(err);
      });
  };
};
