import { IOdataTypeDriveItem } from '../common/odataTypes';

export interface IEnv {
  nonce: string;
  wpAjaxAdminUrl: string;
  props: IEnvProps;
}

export interface IEnvProps {
  pageSize: string;
  hostname: string;
  serverRelativePath: string;
  listTitle: string;
  hideMessageBar: boolean;
  isBasic?: {};
}

export enum ISortDirection {
  default = 'default',
  ascending = 'ascending',
  descending = 'descending'
}

export interface IDriveItemTreeItem {
  key: number;
  item: IOdataTypeDriveItem;
  children: IDriveItemTreeItem[];
  /**
   * When the true the app won't try to load files
   * (= children) for this item.
   */
  locked: boolean;
}

export interface IDriveItemTree {
  root: IRootDriveItem;
}

export interface IRootDriveItem {
  children: IDriveItemTreeItem[];
  locked: false;
}

export interface ITreePosition {
  /**
   * Index of the item relative to the current
   * branch.
   */
  key: number;
}
