import { IToken, TokenRequestError, TokenCache, TokenRequest, TokenRequestV2 } from 'pintra-fx';
import Axios from 'axios';
import { IEnv } from '../definitions';
import { getNumberOrDefault } from '../../utils/typeHelpers';
import { IOdataTypeDriveItem, IOdataTypeSite, IOdataTypeDrive } from '../../common/odataTypes';

export class FilesService {
  /**
   *
   * @param env
   */
  public async getSite(env: IEnv): Promise<IOdataTypeSite | null> {
    const tokenResponse: IToken | TokenRequestError = await TokenCache.getTokenV2(
      new TokenRequestV2('graphSitesReadAll', 'https://graph.microsoft.com/Sites.Read.All'),
      {
        wpAjaxAdminUrl: env.wpAjaxAdminUrl,
        nonce: env.nonce
      }
    );

    if (tokenResponse instanceof Error) {
      console.log(tokenResponse);
      console.log('Unexpected: Could not retrieve a valid access token @DocumentsService.getSite.');
      return null;
    }

    const token = <IToken>tokenResponse;
    const { hostname, serverRelativePath } = env.props;
    const url: string = `https://graph.microsoft.com/beta/sites/${hostname}:/${serverRelativePath}`;

    let response: any;

    try {
      response = await Axios.get(url, {
        headers: {
          Accept: 'application/json',
          ContentType: 'application/json; odata=verbose',
          Authorization: `Bearer ${token.bearer}`
        }
      });
    } catch (error) {
      console.log(error);
      console.log('Unexpected: could not retrieve data from Microsoft Graph @DocumentsService.getSite.');
      return null;
    }

    if (
      response.data &&
      '@odata.context' in response.data &&
      response.data['@odata.context'].indexOf('https://graph.microsoft.com/beta/$metadata#sites') > -1
    ) {
      return response.data as IOdataTypeSite;
    }

    console.log(response);
    console.log('Unexpected: Invalid data received @DocumentsService.getSite.');
    return null;
  }

  /**
   *
   * @param env
   */
  public async getLibrary(env: IEnv): Promise<IOdataTypeDrive | null> {
    const site = await this.getSite(env);

    if (site == null || !('id' in site)) {
      console.log(site);
      console.log('Unexpected: Could not retrieve site @DocumentService:getLibrary.');
      return null;
    }

    const tokenResponse: IToken | TokenRequestError = await TokenCache.getTokenV2(
      new TokenRequestV2('graphSitesReadAll', 'https://graph.microsoft.com/Sites.Read.All'),
      {
        wpAjaxAdminUrl: env.wpAjaxAdminUrl,
        nonce: env.nonce
      }
    );

    if (tokenResponse instanceof Error) {
      console.log(tokenResponse);
      console.log('Unexpected: Could not retrieve a valid access token @DocumentsService.getLibrary.');
      return null;
    }

    const token = <IToken>tokenResponse;
    const { listTitle } = env.props;
    const url: string = `https://graph.microsoft.com/beta/sites/${site.id}/drives`;

    let response: any;

    try {
      response = await Axios.get(url, {
        headers: {
          Accept: 'application/json',
          ContentType: 'application/json; odata=verbose',
          Authorization: `Bearer ${token.bearer}`
        }
      });
    } catch (error) {
      throw error;
    }

    if (
      response.data &&
      '@odata.context' in response.data &&
      response.data['@odata.context'].indexOf('https://graph.microsoft.com/beta/$metadata#drives') > -1 &&
      !!response.data.value
    ) {
      const drives = response.data.value as IOdataTypeDrive[];
      const drive = drives.filter(
        (_drive: IOdataTypeDrive) => _drive.name.toLowerCase() == listTitle.toLowerCase()
      );
      if (drive.length == 1) return drive[0];
    }

    console.log(response.data);
    console.log(`Unexpected: Could not retrieve library with title ${listTitle} @DocumentsService.getLibrary.`);
    return null;
  }

  /**
   *
   * @param env
   */
  public async getInitialNextLink(env: IEnv): Promise<string | null> {
    const library = await this.getLibrary(env);

    if (library == null || !('id' in library)) {
      console.log(library);
      console.log('Unexpected: Could not retrieve library @DocumentService:getFiles.');
      return null;
    }

    return `https://graph.microsoft.com/beta/drives/${library.id}`;
  }

  /**
   *
   * @param nextLink
   * @param env
   * @param next
   */
  public async getFiles(
    nextLink: string,
    env: IEnv,
    next: (nextLink: string) => void,
    finished: () => void
  ): Promise<IOdataTypeDriveItem[] | null> {
    const tokenResponse: IToken | TokenRequestError = await TokenCache.getTokenV2(
      new TokenRequestV2('graphSitesReadAll', 'https://graph.microsoft.com/Sites.Read.All'),
      {
        wpAjaxAdminUrl: env.wpAjaxAdminUrl,
        nonce: env.nonce
      }
    );

    if (tokenResponse instanceof Error) {
      console.log(tokenResponse);
      console.log('Unexpected: Could not retrieve a valid access token @DocumentsService.getFiles.');
      return null;
    }

    const token = <IToken>tokenResponse;
    let response: any;

    try {
      response = await Axios.get(nextLink, {
        headers: {
          Accept: 'application/json',
          ContentType: 'application/json; odata=verbose',
          Authorization: `Bearer ${token.bearer}`
        }
      });
    } catch (error) {
      console.log(error);
      console.log('Unexpected: Could not connect to MS Graph @DocumentsService.getFiles.');
      return null;
    }

    if (
      response.data &&
      '@odata.context' in response.data &&
      response.data['@odata.context'].indexOf('https://graph.microsoft.com/beta/$metadata#drives') > -1 &&
      !!response.data.value
    ) {
      if ('@odata.nextLink' in response.data) {
        next(response.data['@odata.nextLink']);
      } else {
        finished();
      }
      return response.data.value as IOdataTypeDriveItem[];
    }

    console.log(response.data);
    console.log(`Unexpected: Could not retrieve items from ${nextLink} @DocumentsService.getFiles.`);
    return null;
  }
}
