import { combineReducers } from 'redux';
import { StatusReducer } from '../../common/redux/Status';
import { IStatus } from '../../common/definitions';
import { NextLinkReducer } from './nextLink';
import { IDriveItemTree, ITreePosition } from '../definitions';
import { TreeReducer } from './tree';
import { PositionReducer } from './position';
import { BaseLinkReducer } from './baseLink';

export interface IDocumentsAppState {
  tree: IDriveItemTree;
  position: ITreePosition[];
  status: IStatus;
  baseLink: string | null;
  nextLink: string | null;
}

export const rootReducer = combineReducers<IDocumentsAppState>({
  tree: TreeReducer,
  position: PositionReducer,
  status: StatusReducer,
  baseLink: BaseLinkReducer,
  nextLink: NextLinkReducer
});
