import { Label } from 'office-ui-fabric-react/lib/Label';
import React = require('react');

export interface IOfLabelProps {
  className: string;
  innerText: string;
}

export interface IClickableOfLabelProps {
  className: string;
  innerText: string;
  onClick: (event: React.MouseEvent<HTMLLabelElement>) => void;
}

export const OfLabel: React.SFC<IOfLabelProps> = props => (
  <Label
    className={props.className}
    style={{
      paddingTop: '0px',
      paddingBottom: '0px'
    }}>
    {props.innerText}
  </Label>
);

export const OfClickableLabel: React.SFC<IClickableOfLabelProps> = props => (
  <Label
    className={props.className}
    style={{
      paddingTop: '0px',
      paddingBottom: '0px',
      textDecoration: 'underline',
      color: '#0073aa',
      cursor: 'pointer'
    }}
    onClick={props.onClick}>
    {props.innerText}
  </Label>
);
