import * as React from 'react';

import { DetailsListLayoutMode, Selection, IColumn, SelectionMode } from 'office-ui-fabric-react/lib/DetailsList';
import { ShimmeredDetailsList } from 'office-ui-fabric-react/lib/ShimmeredDetailsList';
import { Link } from 'office-ui-fabric-react/lib/Link';
import { MarqueeSelection } from 'office-ui-fabric-react/lib/MarqueeSelection';
import { Icon } from 'office-ui-fabric-react/lib/Icon';

import { IDocumentsAppState } from '../redux/store';
import { connect } from 'react-redux';
import { initializeIcons } from 'office-ui-fabric-react/lib/Icons';
import { getIcon } from './IconHelpers';

import moment = require('moment');
import numeral = require('numeral');

import { _Notifications } from './_Notifications';
import { IEnv, ISortDirection, IDriveItemTree, IDriveItemTreeItem, ITreePosition } from '../definitions';
import { FilesService } from '../services/FilesService';
import { ItemsThunk } from '../redux/tree';
import { getCurrentBranch, getTreeItem, updateTreeItem } from '../common/helpers';
import { PositionUpdatedAction } from '../redux/position';
import { BaseLinkUpdatedAction } from '../redux/baseLink';
import { NextLinkUpdatedAction } from '../redux/nextLink';
import { FolderNavigation } from './Breadcrumb';
import { IStatus, StatusType } from '../../common/definitions';
import { StatusChangedAction } from '../../common/redux/Status';
import { OfClickableLabel, OfLabel } from '../common/Label';
import { asBool } from '../../utils/typeHelpers';

initializeIcons(/* optional base url */);

const columns = [
  {
    key: 'icon',
    name: 'Icon',
    iconName: 'Page',
    isIconOnly: true,
    minWidth: 32,
    maxWidth: 32,
    isResizable: true,
    onRender: (treeItem: IDriveItemTreeItem) => (
      <Icon className={'pintraDocumentsIcon'} style={{ fontSize: '0.8rem' }} iconName={getIcon(treeItem.item)} />
    )
  },
  { key: 'name', name: 'Name', minWidth: 250, isResizable: true },
  { key: 'modified', name: 'Modified', minWidth: 150, isResizable: true },
  { key: 'modifiedBy', name: 'Modified By', minWidth: 150, isCollapsable: true, isResizable: true },
  { key: 'size', name: 'Size', minWidth: 100, isCollapsable: true, isResizable: true }
];

export interface IDocumentsProps {
  env: IEnv;
  tree: IDriveItemTree;
  position: ITreePosition[];
  status: IStatus;
  getRoot: () => void;
  getFolder: (position: ITreePosition[], driveItemId: string) => void;
  notSupported: (message: string) => void;
}

export interface IDocumentsState {
  tree: IDriveItemTree; // The "static" tree of all items
  position: ITreePosition[]; // The current "user" position in the tree
  items: IDriveItemTreeItem[]; // The (possibly sorted) branch of the tree currently viewed by the user
  sorting: {
    name: ISortDirection;
    modified: ISortDirection;
    modifiedBy: ISortDirection;
  };
}

export interface IStateProps {
  tree: IDriveItemTree;
  position: ITreePosition[];
  baseLink: string;
  status: IStatus;
}

export interface IDispatchProps {
  getRoot: () => void;
  getFiles: (position: ITreePosition[]) => void;
  getFolder: (position: ITreePosition[], baseLink: string, driveItemId: string) => void;
  isFetching: () => void;
  notSupported: (message: string) => void;
}

export interface IOwnProps {
  env: IEnv;
}

export class _Documents extends React.Component<IDocumentsProps, IDocumentsState> {
  private _selection: Selection;

  public constructor(props: IDocumentsProps) {
    super(props);

    const { tree, position } = this.props;

    this.state = {
      tree,
      position,
      items: [],
      sorting: {
        name: ISortDirection.default,
        modified: ISortDirection.default,
        modifiedBy: ISortDirection.default
      }
    };

    this._selection = new Selection({
      onSelectionChanged: () => false
    });
  }

  public componentDidMount() {
    const { getRoot } = this.props;
    getRoot();
  }

  public componentDidUpdate(prevProps: IDocumentsProps, prevState: IDocumentsState): void {
    const { tree, position } = this.props;
    if (prevProps.tree != tree || prevProps.position != position) {
      this.setState({ tree, position }, () => this.setState({ items: getCurrentBranch(tree, position) }));
    }
  }

  public render(): React.ReactElement<IDocumentsProps> {
    const { items } = this.state;
    const { env, status } = this.props;
    return (
      <React.Fragment>
        {asBool(env.props.hideMessageBar) === false && <_Notifications />}
        {!env.props.isBasic && <FolderNavigation env={env} />}
        <MarqueeSelection selection={this._selection}>
          <ShimmeredDetailsList
            items={items}
            selectionMode={SelectionMode.none}
            enableShimmer={status.type === StatusType.FETCHING}
            columns={columns}
            setKey="set"
            layoutMode={DetailsListLayoutMode.justified}
            selection={this._selection}
            selectionPreservedOnEmptyClick={true}
            ariaLabelForSelectionColumn="Toggle selection"
            ariaLabelForSelectAllCheckbox="Toggle selection for all items"
            onRenderItemColumn={this.renderItemColumn}
            className={'pintraRecentList'}
            onColumnHeaderClick={this.sort}
          />
        </MarqueeSelection>
      </React.Fragment>
    );
  }

  private sort = (event: React.MouseEvent<HTMLElement>, column: any) => {
    const { tree, position, sorting } = this.state;
    const sortedItems: IDriveItemTreeItem[] = [...getCurrentBranch(tree, position)];
    const key = column.key;

    if ('name,modified,modifiedBy'.indexOf(key) == -1) {
      return;
    }

    const currentSorting = (sorting as any)[column.key];
    const sorter =
      currentSorting == ISortDirection.default || currentSorting == ISortDirection.descending ? 1 : -1;

    sortedItems.sort((itemA, itemB) => {
      const valueA = ((itemA.item as any)[column.key] as any).toLowerCase();
      const valueB = ((itemB.item as any)[column.key] as any).toLowerCase();
      if (valueA > valueB) return sorter;
      if (valueA < valueB) return -sorter;
      return 0;
    });
    const _sorting = { ...sorting };
    (_sorting as any)[column.key] =
      currentSorting == ISortDirection.default || currentSorting == ISortDirection.descending
        ? ISortDirection.ascending
        : ISortDirection.descending;

    this.setState({ items: sortedItems, sorting: _sorting });
  };

  private renderItemColumn = (treeItem: IDriveItemTreeItem, index: number, column: IColumn) => {
    const { getFolder, notSupported, env } = this.props;
    const { position } = this.state;

    if (column.onRender) {
      return column.onRender(treeItem, index);
    }

    const item = treeItem.item;

    switch (column.key) {
      case 'name':
        return !!item.folder ? (
          !env.props.isBasic ? (
            <OfClickableLabel
              className={'pintraDocumentsName'}
              onClick={(event: React.MouseEvent<HTMLLabelElement>) => {
                event.stopPropagation();
                getFolder([...position].concat([{ key: treeItem.key }]), item.id);
              }}
              innerText={item.name}
            />
          ) : (
            <Link
              className={'pintraDocumentsName'}
              onClick={(event: React.MouseEvent<HTMLLabelElement>) =>
                notSupported('To support folder navigation please install the INTRANET edition.')
              }
              href={item.webUrl}
              target={'_blank'}>
              {item.name}
            </Link>
          )
        ) : (
          <Link className={'pintraDocumentsName'} href={item.webUrl} target={'_blank'}>
            {item.name}
          </Link>
        );
      case 'modified':
        return (
          <OfLabel
            className={'pintraDocumentsModified'}
            innerText={
              !!item.lastModifiedDateTime ? moment(item.lastModifiedDateTime).format('DD/MM/YYYY HH:mm') : ''
            }
          />
        );
      case 'modifiedBy':
        return (
          <OfLabel
            className={'pintraDocumentsModifiedBy'}
            innerText={
              !!item.lastModifiedBy && !!item.lastModifiedBy.user && !!item.lastModifiedBy.user.displayName
                ? item.lastModifiedBy.user.displayName
                : ''
            }
          />
        );
      case 'size':
        return (
          <OfLabel
            className={'pintraDocumentsSize'}
            innerText={!!item.size ? numeral(item.size).format('0.0 b') : ''}
          />
        );
      default:
        return <span />;
    }
  };
}

const mapStateToProps = (state: IDocumentsAppState) => ({
  tree: state.tree,
  position: state.position,
  baseLink: state.baseLink,
  status: state.status
});

const mapDispatchToProps = (dispatch: any, ownProps: IOwnProps) => {
  const { env } = ownProps;
  return {
    /**
     * Gets the base link, then configure the initial next link by adding the root/children
     * portion and then kicks of the initial (iterative) fetching of drive items
     */
    getRoot: () => {
      dispatch(
        StatusChangedAction({
          type: StatusType.FETCHING,
          statusMessage: ''
        })
      );
      const fileService = new FilesService();
      fileService
        .getInitialNextLink(env)
        .then((baseLink: string | null) => {
          if (baseLink == null) {
            throw 'Could not retrieve base link @Documents.mapDispatchToProps.getRoot';
          }
          // Cache the base link
          dispatch(BaseLinkUpdatedAction(baseLink));
          // Update the next link
          dispatch(NextLinkUpdatedAction(`${baseLink}/root/children?$top=${env.props.pageSize}`));
          // Trigger the iterative fetching of drive items
          dispatch(ItemsThunk(env, []));
        })
        .catch((err: Error) => {
          dispatch(
            StatusChangedAction({
              type: StatusType.ERROR,
              statusMessage: 'Unexpected: Could not contact the website. Please contact your administrator.'
            })
          );
          console.debug(err);
        });
    },
    /**
     * Kicks of the (iterative) fetching of drive items given the current next link
     * which is most likely updated e.g. by a user clicking a folder drive item
     */
    getFiles: (position: ITreePosition[]) => {
      dispatch(ItemsThunk(env, position));
    },
    /**
     * Updates the tree position after the user clicks in the tree e.g. on a folder drive
     * item and then updates the next link accordingly
     */
    getFolder: (position: ITreePosition[], baseLink: string, driveItemId: string) => {
      dispatch(NextLinkUpdatedAction(`${baseLink}/items/${driveItemId}/children`));
      dispatch(PositionUpdatedAction(position));
    },
    isFetching: () => {
      dispatch(
        StatusChangedAction({
          type: StatusType.FETCHING,
          statusMessage: ''
        })
      );
    },
    notSupported: (message: string) =>
      dispatch(
        StatusChangedAction({
          type: StatusType.NOTSUPPORTED,
          statusMessage: message
        })
      )
  };
};

const mergeProps = (stateProps: IStateProps, dispatchProps: IDispatchProps, ownProps: IOwnProps) => {
  return {
    getRoot: dispatchProps.getRoot,
    notSupported: dispatchProps.notSupported,
    ...ownProps,
    tree: stateProps.tree,
    position: stateProps.position,
    status: stateProps.status,
    getFolder: (position: ITreePosition[], driveItemId: string) => {
      const currentItem = getTreeItem(stateProps.tree, position) as IDriveItemTreeItem;
      dispatchProps.getFolder(position, stateProps.baseLink, driveItemId);
      if (!currentItem.locked) {
        dispatchProps.isFetching();
        dispatchProps.getFiles(position);
        currentItem.locked = true;
        updateTreeItem(stateProps.tree, position, currentItem);
      }
    }
  };
};

export const Documents = connect<IStateProps, IDispatchProps, IOwnProps, IDocumentsProps>(
  mapStateToProps,
  mapDispatchToProps,
  mergeProps
)(_Documents);
